.avatar {
  align-items: center;
  background-color: var(--color-marketing-teal-background-5);
  border-radius: 50%;
  color: var(--color-marketing-teal-foreground-5);
  display: inline-flex;
  font-size: var(--font-size-large-2);
  font-weight: var(--font-weight-bold);
  height: 48px;
  justify-content: center;
  line-height: 48px;
  overflow: hidden;
  width: 48px;
}
.avatar > svg {
  height: 100%;
  width: 100%;
}
.avatar > img {
  height: 48px;
  object-fit: cover;
  width: 48px;
}
.avatar--teal {
  background-color: var(--color-marketing-teal-background-5);
  color: var(--color-marketing-teal-foreground-5);
}
.avatar--light-teal {
  background-color: var(--color-marketing-teal-background-3);
  color: var(--color-marketing-teal-foreground-3);
}
.avatar--green {
  background-color: var(--color-marketing-green-background-3);
  color: var(--color-marketing-green-foreground-3);
}
.avatar--lime {
  background-color: var(--color-marketing-lime-background-4);
  color: var(--color-marketing-lime-foreground-4);
}
.avatar--yellow {
  background-color: var(--color-marketing-yellow-background-3);
  color: var(--color-marketing-yellow-foreground-3);
}
.avatar--orange {
  background-color: var(--color-marketing-orange-background-3);
  color: var(--color-marketing-orange-foreground-3);
}
.avatar--magenta {
  background-color: var(--color-marketing-magenta-background-4);
  color: var(--color-marketing-magenta-foreground-4);
}
.avatar--32,
.avatar--32 > img {
  font-size: var(--font-size-medium);
  height: 32px;
  line-height: 32px;
  width: 32px;
}
.avatar--40,
.avatar--40 > img {
  font-size: var(--font-size-large-1);
  height: 40px;
  line-height: 40px;
  width: 40px;
}
.avatar--48,
.avatar--48 > img {
  font-size: var(--font-size-large-2);
  height: 48px;
  line-height: 48px;
  width: 48px;
}
.avatar--56,
.avatar--56 > img {
  font-size: var(--font-size-giant-1);
  height: 56px;
  line-height: 56px;
  width: 56px;
}
.avatar--64,
.avatar--64 > img {
  font-size: var(--font-size-giant-2);
  height: 64px;
  line-height: 64px;
  width: 64px;
}
.avatar--96,
.avatar--96 > img {
  font-size: var(--font-size-giant-3);
  height: 96px;
  line-height: 96px;
  width: 96px;
}
.avatar--128,
.avatar--128 > img {
  font-size: var(--font-size-giant-4);
  height: 128px;
  line-height: 128px;
  width: 128px;
}
