.video-player {
  position: relative;
}
.video-player video {
  height: 100%;
  width: 100%;
}
.video-player__overlay {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 0;
  color: var(--color-neutral-0);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  position: absolute;
  right: 0;
  text-align: center;
  top: 0;
}
.video-player__overlay-text {
  margin: 20px 20px 0;
}
.video-player__overlay--hidden {
  display: none;
}
.video-player__report-button > svg {
  -webkit-margin-end: var(--spacing-50);
          margin-inline-end: var(--spacing-50);
}
.video-player .shaka-overflow-menu svg {
  height: 24px;
  margin-left: 10px;
  margin-right: 10px;
  width: 24px;
}
.video-player .shaka-play-button {
  background: none;
  box-shadow: none;
  height: auto;
  padding: 0;
  width: auto;
}
.video-player .icon--video-play,
.video-player .shaka-play-button svg {
  height: 48px;
  width: 48px;
}
.video-player .shaka-controls-button-panel {
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 8px;
}
.video-player--poster .shaka-scrim-container {
  visibility: hidden;
}
