a.cta-btn {
  border: 1px solid;
  box-sizing: border-box;
  font-family: inherit;
  margin: 0;
  text-align: center;
  text-decoration: none;
  vertical-align: bottom;
  background-color: var(--cta-btn-background-color, var(--color-background-primary));
  border-color: currentColor;
  border-radius: var(--cta-btn-border-radius, calc(40px / 2));
  color: var(--cta-btn-foreground-color, var(--color-foreground-primary));
  display: inline-block;
  font-size: var(--font-size-default);
  max-width: 100%;
  padding: 9.5px 20px;
}
a.cta-btn--fixed-height {
  height: 40px;
}
a.cta-btn--truncated {
  height: 40px;
}
a.cta-btn--truncated,
a.cta-btn--truncated span {
  line-height: 1.4em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
a.cta-btn:focus:not(:focus-visible) {
  outline: none;
}
a.cta-btn:visited {
  color: var(--cta-btn-visited-foreground-color, var(--color-foreground-primary));
}
a.cta-btn:focus,
a.cta-btn:hover {
  background-color: var(--cta-btn-hover-background-color, var(--color-foreground-primary));
  border-color: var(--cta-btn-hover-border-color, var(--color-foreground-primary));
  color: var(--cta-btn-hover-foreground-color, var(--color-background-primary));
}
a.cta-btn:not([href]),
a.cta-btn[aria-disabled="true"] {
  background-color: var(--cta-btn-disabled-background-color, var(--color-background-primary));
  border-color: var(--cta-btn-disabled-border-color, var(--color-stroke-disabled));
  color: var(--cta-btn-disabled-foreground-color, var(--color-foreground-disabled));
}
a.cta-btn--fluid {
  width: 100%;
}
span.cta-btn__cell {
  align-items: center;
  display: flex;
  width: 100%;
  justify-content: center;
}
span.cta-btn__cell--fixed-height {
  display: inline-flex;
}
span.cta-btn__cell--fixed-height > svg {
  align-self: baseline;
  max-width: calc(100% - 32px);
}
span.cta-btn__cell--truncated {
  display: inline-flex;
}
span.cta-btn__cell--truncated > svg {
  align-self: baseline;
  max-width: calc(100% - 32px);
}
a.cta-btn svg.icon {
  align-self: center;
  flex-shrink: 0;
  height: 10px;
  width: 10px;
}
a.cta-btn svg.icon:first-child {
  -webkit-margin-end: 8px;
          margin-inline-end: 8px;
}
a.cta-btn svg.icon:last-child {
  -webkit-margin-start: 8px;
          margin-inline-start: 8px;
}
a.cta-btn svg.icon:only-child {
  margin: 0;
}
span.cta-btn__cell--fixed-height svg.icon {
  align-self: center;
  height: 1rem;
  overflow: visible;
  width: 1rem;
}
a.cta-btn--truncated,
a.cta-btn--truncated span {
  line-height: 1.4em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
a.cta-btn--large {
  border-radius: 24px;
  display: inline-flex;
  font-size: var(--font-size-medium);
  min-height: 48px;
}
a.cta-btn--large-truncated {
  border-radius: 24px;
  display: inline-flex;
  font-size: var(--font-size-medium);
  height: 48px;
}
a.cta-btn--large-truncated,
a.cta-btn--large-truncated span {
  line-height: 1.4em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
a.cta-btn--large-fixed-height {
  border-radius: 24px;
  display: inline-flex;
  font-size: var(--font-size-medium);
  height: 48px;
}
[dir="rtl"] a.cta-btn svg.icon--arrow-right-24 {
  transform: rotate(180deg);
}
