.badge {
  align-items: center;
  background-color: var(--badge-background-color, var(--color-background-attention));
  border-radius: 16px;
  color: var(--badge-foreground-color, var(--color-foreground-on-attention));
  display: inline-flex;
  font-size: 10px;
  height: 16px;
  justify-content: center;
  min-width: 8px;
  padding: 2px 6px;
  white-space: nowrap;
}
