.eek {
  align-items: stretch;
  display: inline-flex;
  font-family: Arial, sans-serif;
  font-weight: 700;
  height: 28px;
  position: relative;
}
.eek__container {
  align-items: center;
  border: 1px solid #000;
  border-radius: 2px 0 0 2px;
  border-right: none;
  display: inline-flex;
}
.eek .icon--eek-arrow {
  width: 11px;
}
.eek__arrow {
  overflow: hidden;
  width: 17px;
}
.eek__arrow::before {
  border: 0.5px solid #000;
  border-radius: 2px;
  content: "";
  display: block;
  /* stylelint-disable-next-line number-max-precision */
  height: 19.79898987px;
  margin-top: 2.3px;
  position: relative;
  right: 12px;
  transform: rotate(45deg);
  /* stylelint-disable-next-line number-max-precision */
  width: 19.79898987px;
}
.eek--rating-1 .eek__container {
  background: #00a650;
}
.eek--rating-1 svg.icon--eek-arrow {
  fill: #00a650;
}
.eek--rating-2 .eek__container {
  background: #adbd38;
}
.eek--rating-2 svg.icon--eek-arrow {
  fill: #adbd38;
}
.eek--rating-3 .eek__container {
  background: #b3db18;
}
.eek--rating-3 svg.icon--eek-arrow {
  fill: #b3db18;
}
.eek--rating-4 .eek__container {
  background: #fff200;
}
.eek--rating-4 svg.icon--eek-arrow {
  fill: #fff200;
}
.eek--rating-5 .eek__container {
  background: #fab20b;
}
.eek--rating-5 svg.icon--eek-arrow {
  fill: #fab20b;
}
.eek--rating-6 .eek__container {
  background: #f25c19;
}
.eek--rating-6 svg.icon--eek-arrow {
  fill: #f25c19;
}
.eek--rating-7 .eek__container {
  background: #ed1c24;
}
.eek--rating-7 svg.icon--eek-arrow {
  fill: #ed1c24;
}
.eek__rating {
  color: #fff;
  display: inline-block;
  font-size: 18px;
  margin-left: 8px;
  text-shadow: -0.5px 0.5px 0 #000, 0.5px 0.5px 0 #000, 0.5px -0.5px 0 #000, -0.5px -0.5px 0 #000;
}
.eek__rating-range {
  align-items: center;
  background-color: #fff;
  border-radius: 1px 0 0 1px;
  display: inline-flex;
  flex-direction: column;
  height: 24px;
  margin: 1px;
  padding: 0 1px;
}
.eek__rating-range > .icon--eek-range-arrow {
  height: 6px;
  width: 5px;
}
.eek__rating-range > span {
  font-size: 8px;
}
/*
In Safari the EEK icon is not aligned properly as well as the text is broken
Changed a bit of styles there only in order to make it work
*/
@media not all and (min-resolution: 0.001dpcm) {
  @supports (-webkit-appearance: none) {
    .eek__rating {
      text-shadow: none;
      -webkit-text-stroke: 0.5px;
      -webkit-text-stroke-color: #000;
    }
  }
}
